import * as React from 'react';
import { utils } from 'gqlx-js';

export interface ContentProps {}

export interface ContentState {
  schema: string;
  source: string;
}

export class Content extends React.Component {
  private changeSchema = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const source = e.target.value;
    const gql = utils.parseDynamicSchema(source);
    const schema = gql.schema.text;
    this.setState({
      source,
      schema,
    });
  };

  render() {
    const { source, schema } = this.state;
    return (
      <div>
        <textarea onChange={this.changeSchema} value={source} />
        <output>{schema}</output>
      </div>
    );
  }
}
